<template>
    <div class="update">
      <!-- <el-button type="text" @click="dvp = true">点击打开 Dialog</el-button> -->
      <el-table
    :data="tableData"
    stripe
    style="width: 100%">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
      prop="id"
      label="商品ID"
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label="商品名称"
      width="180">
    </el-table-column>
    <el-table-column
      prop="jig"
      label="价格">
    </el-table-column>
    <el-table-column
      prop="caozuo"
      label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">
          编辑
        </el-button>

        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>

      <el-button
          size="mini"
          @click="handleEdit">
          编辑
        </el-button>
<el-dialog
  title="修改列表"
  :visible.sync="dvp"
  width="30%"
  :before-close="handleClose">
  
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="类目选择" >
    <el-button type="primary">类目选择</el-button>
  </el-form-item>
  <el-form-item label="商品ID" prop="id">
    <el-input v-model="ruleForm.id"></el-input>
  </el-form-item>
  <el-form-item label="商品名称" prop="name">
    <el-input v-model="ruleForm.name"></el-input>
  </el-form-item>
  <el-form-item label="商品价格" prop="jig">
    <el-input v-model="ruleForm.jig"></el-input>
  </el-form-item>
  <el-form-item label="商品图片" prop="img">
    <el-button type="primary">上传图片</el-button>
  </el-form-item>
  <el-form-item label="商品描述" prop="desc">
    <el-input type="textarea" v-model="ruleForm.desc"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')">确认修改</el-button>
    <el-button @click="resetForm('ruleForm')">重置</el-button>
  </el-form-item>
</el-form>
  <!-- <span slot="footer" class="dialog-footer">
    <el-button @click="dvp = false">取 消</el-button>
    <el-button type="primary" @click="dvp = false">确 定</el-button>
  </span> -->
</el-dialog>
    </div>
  </template>
  <script>
  export default {
    data() {
      return {
        tableData: [{
          id: '2016-05-02',
          name: '王小虎',
          jig: '上海市普陀区金沙江路 1518 弄'
        }, {
          id: '2016-05-04',
          name: '王小虎',
          jig: '上海市普陀区金沙江路 1517 弄'
        },],
        dvp: false,
        ruleForm: {
          id:'',
          name: '',
          jig:'',
          desc:'',
          img:'',
          date1: '',
          date2: '',
        },
        rules: {
          id:[
            {required: true, message: '请输入商品ID', trigger: 'blur' }
          ],
          name: [
            { required: true, message: '请输入商品名称', trigger: 'blur' },
            // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          jig: [
            { required: true, message: '请输入商品价格', trigger: 'blur' }
          ],
          
        }
      };
    },

    methods: {
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
    // 编辑
    handleEdit(index, row) {
        this.dvp = true
        console.log(row);
        this.ruleForm=row
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('修改成功');
            this.dvp=false
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
  </script>
  
  <style  lang="less" scoped>
   
  </style>